	.display-1 {
		font-size: 3.68rem;
	}
}
@media (max-width: 768px) {
	.display-1 {
		font-size: 3.22rem;
		font-size: calc(2.26rem + (4.6 - 2.26) * ((100vw - 20rem) / (48 - 20)));
		line-height: calc(
			1.1 * (2.26rem + (4.6 - 2.26) * ((100vw - 20rem) / (48 - 20)))
		);
	}
