.mbr-arrow-up a {
	background: rgba(0, 0, 0, 0.2);
	border-radius: 50%;
	color: #fff;
	display: inline-block;
	height: 60px;
	width: 60px;
	border: 2px solid #fff;
	outline-style: none !important;
	position: relative;
	text-decoration: none;
	transition: all 0.3s ease-in-out;
