	margin: 0.6rem 0.6rem;
	white-space: normal;
	transition: all 0.2s ease-in-out;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	word-break: break-word;
}

.btn-sm {
	font-weight: 600;
	letter-spacing: 0px;
	transition: all 0.3s ease-in-out;
}

.btn-md {
	font-weight: 600;
	letter-spacing: 0px;
	transition: all 0.3s ease-in-out;
}

.btn-lg {
