}
.btn-form:hover {
	cursor: pointer;
}

nav .mbr-section-btn {
	margin-left: 0rem;
	margin-right: 0rem;
}

/*! Btn icon margin */
.btn .mbr-iconfont,
.btn.btn-sm .mbr-iconfont {
	order: 1;
	cursor: pointer;
	margin-left: 0.5rem;
	vertical-align: sub;
}

.btn.btn-md .mbr-iconfont,
.btn.btn-md .mbr-iconfont {
	margin-left: 0.8rem;
}

.mbr-regular {
